.app-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh; /* Ensure the container covers the whole viewport height */
  padding-bottom: 100px; /* Reserve space for the image */
}

.app-container h1 {
  margin-bottom: 20px;
  text-align: center;
  color: red;
}

.bottom-right-image {
  position: absolute;
  bottom: 10px; /* Adjust the bottom position */
  right: 10px; /* Adjust the right position */
  width: 300px; /* Adjust the width as needed */
  height: auto; /* Maintain aspect ratio */
}
