.registration-form {
  /* Center the form horizontally */
  display: flex;
  flex-direction: column;
  align-items: center;

  /* Add spacing and alignment for form elements */
  padding: 20px;
}

/* Style the input fields */
.registration-form input[type="tel"],
.registration-form select {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

/* Style the labels */
.registration-form label {
  font-weight: bold;
  margin-bottom: 8px;
}

/* Style the submit button */
.registration-form button {
  padding: 10px 20px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.registration-form button:hover {
  background-color: red;
}
